import { formLoaderActions } from "./../redux/slices/formLoader/formLoaderSlice";
import { orderActions } from "../redux/slices/order/orderSlice";
import http from "./http.service";
import { AppDispatch } from "redux/store";
import Promisable from "./promisable.service";
import { NavigateFunction } from "react-router-dom";

const url = `/orders`;

const OrderService = {
  getAllOrders: async (dispatch?: AppDispatch) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}`)
    );

    if (success) {
      const { allOrders } = success.data.data;
      dispatch?.(orderActions.setOrders(allOrders));
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },
  getAccountWithMostOrders: async (dispatch?: AppDispatch) => {
    dispatch?.(orderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/account-with-most-orders`)
    );

    if (success) {
      const { accountWithMostOrders } = success.data.data;

      dispatch?.(orderActions.setMostOrders(accountWithMostOrders));
    }

    dispatch?.(orderActions.setLoading(false));

    return [success, error];
  },
};

export default OrderService;
