import { StyledTableCell, StyledTableRow } from "components/templates/Tables";
import {
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableContainer,
} from "@mui/material";
import TableLoadingWrapper from "components/templates/TableLoadingWrapper";
import Button from "components/atoms/Button";
import { useNavigate } from "react-router-dom";
import { BiLinkExternal } from "react-icons/bi";
import { useAppSelector } from "redux/hooks";
import { useAppDispatch } from "redux/hooks";
import OrderService from "services/order.service";
import { useEffect } from "react";

export default function OrdersList() {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const orders = useAppSelector((state) => state.order.orders);
  const loading = useAppSelector((state) => state.order.loading);

  useEffect(() => {
    OrderService.getAllOrders(dispatch);
  }, [dispatch]);

  return (
    <div>
      <TableContainer>
        <Table
          aria-label="customized table"
          sx={{
            minWidth: "100%",
            borderSpacing: "0 10px",
            borderBottomWidth: "0px",
            borderCollapse: "separate",
          }}
        >
          <TableLoadingWrapper
            container
            coloumns={4}
            loading={false}
            message="No active orders available"
            length={10}
          >
            <TableHead>
              <TableRow>
                <StyledTableCell>Id</StyledTableCell>
                <StyledTableCell>First Name</StyledTableCell>
                <StyledTableCell>Last Name</StyledTableCell>
                <StyledTableCell>Phone</StyledTableCell>
                <StyledTableCell>Order Status</StyledTableCell>
                <StyledTableCell>Store Name</StyledTableCell>
                <StyledTableCell>Total</StyledTableCell>
                <StyledTableCell>Date</StyledTableCell>
                <StyledTableCell></StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {orders.map((order: any) => (
                <StyledTableRow key={order.id}>
                  <StyledTableCell>{order.id}</StyledTableCell>
                  <StyledTableCell>{order.firstName}</StyledTableCell>
                  <StyledTableCell>{order.lastName}</StyledTableCell>
                  <StyledTableCell>{order.phone}</StyledTableCell>
                  <StyledTableCell>{order.orderStatus}</StyledTableCell>
                  <StyledTableCell>{order.storeName}</StyledTableCell>
                  <StyledTableCell>{order.total}</StyledTableCell>
                  <StyledTableCell>{order.date}</StyledTableCell>
                  <StyledTableCell align="right">
                    <Button
                      // size="large"
                      variant="contained"
                      color="primary"
                      disableElevation
                      onClick={() => navigate(`/orders-details`)}
                      sx={{ minWidth: "auto" }}
                    >
                      <BiLinkExternal /> Details
                    </Button>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </TableLoadingWrapper>
        </Table>
      </TableContainer>
    </div>
  );
}
